@import 'base/functions';
@import 'base/mixins';
@import 'base/colors';

@font-face {
    font-family: 'auretech';
    src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
        url(data:application/font-woff;charset=utf-8;base64,) format('woff');
    font-weight: normal;
    font-style: normal;
}

$auretech : 'auretech';

html body.imperial-styleguide-bookmarklet {
    background: $black !important;

    div,
    section,
    header,
    footer {
        background: $black !important;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-family: $auretech !important;
        color: $white !important;
        font-weight: 700;

        @include neon-glow($light-imperial-red);
    }

    p,
    li,
    a {
        font-family: $auretech !important;
        color: $white !important;
        text-decoration: underline !important;
    }

    .neat-tv-static-screen-scan-effect {
        position: fixed;

        opacity: 0.45;

        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        z-index: 0;

        canvas {
            height: 100%;
            width: 100%;
        }
    }
}
